
html {
  width: 100%;
  height: 100%;
  font-family: "NanumBarunGothic";
  color: #333;
  background: #FFF;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
html *,
  html *::before,
  html *::after {
    -webkit-box-sizing: inherit;
    box-sizing: inherit;
}
html, body, body div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, figure, footer, header, menu, nav, section, time, mark, audio, video, details, summary, input, button {
  margin: 0;
  padding: 0;
}
body {
  width: 100%;
  height: 100%;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img {
  border: 0;
  margin: 0 auto;
}
  /*
  TODO think about hanlding inheritence differently, maybe letting IE6 fail a bit...
*/
address,
caption,
cite,
code,
dfn,
em,
th,
var {
  font-style: normal;
  font-weight: normal;
}
a {
  color: #333;
  text-decoration: none;
}
ol,
ul,
li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
q:before,
q:after {
  content: '';
}
abbr,
acronym {
  border: 0;
  font-variant: normal;
}
  /* to preserve line-height and selector appearance */
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
text {
  font-size: 14px !important;
}
input,
textarea,
select {
  border: 0px;
  background: none;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
  /*to enable resizing for IE*/
input,
textarea,
select {
  *font-size: 100%;
}
  /*because legend doesn't inherit in IE */
legend {
  color: #000;
}
img {
  display: block;
}
button {
  outline: 0;
  border: none;
  cursor: pointer;
  background: none;
}
a, botton {
  text-decoration: none;
  transition: .2s ease-in-out;
  -webkit-transition: .2s ease-in-out;
  -moz-transition: .2s ease-in-out;
  -ms-transition: .2s ease-in-out;
  -o-transition: .2s ease-in-out;
}
